* {
  margin: 0;
  padding: 0;
}

.header__logo {
  width: 100%;
  height: 111px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #ffffff;
}

.header__logo--image {
  width: 201px;
  height: 52px;
}

.viewfinder {
  background: linear-gradient(#bfcdd0, #dde9ed);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 475px;
}

.viewfinder__photo {
  background-image: url("https://place-hold.it/240x200/red");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  width: 240px;
  height: 200px;
}

.viewfinder__card {
  position: relative;
  background-color: #ffffff;
  height: 367px;
}

.viewfinder__card-title {
  display: flex;
}

.viewfinder__card-title-titlebox {
  display: flex;
  flex-direction: column;
}

.viewfinder__card-title-rectangule {
  height: 42px;
  width: 5px;
  margin: 20px 15px 0 0;
}

.viewfinder__card-title-name {
  padding: 20px 14px 5px 0;
  font-family: 'Comic Sans MS', 'Comic Sans', 'TSCu_Comic';
  font-size: 18px;
  line-height: 20px;
}

.viewfinder__card-title-job {
  padding: 0 14px 5px 0;
  font-family: 'Comic Sans MS', 'Comic Sans', 'TSCu_Comic';
  font-size: 18px;
  line-height: 20px;
}

.viewfinder__icons {
  display: flex;
  justify-content: flex-start;
  margin: 10px 0 10px 10px;
}

.viewfinder__icons-box {
  margin-left: 10px;
  border-radius: 22px;
  width: 32px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.viewfinder__tags {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 5px 20px;
}

.viewfinder__tags-text {
  width: 36px;
  height: 18px;
  font-size: 13px;
  border-radius: 4px;
  margin-right: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  padding: 0 5px;
}

.viewfinder__button {
  position: absolute;
  top: -40px;
  left: 0;
  width: 75px;
  height: 30px;
}

.viewfinder__button-reset {
  background-color: #bfcdd0;
  border-radius: 15px;
  border: 1px solid #ffffff;
  text-transform: uppercase;
  font-family: 'OpenSans', serif;
  font-size: 12px;
  color: #54585a;
  padding: 5px 10px;
}

.viewfinder__button-icono {
  margin-right: 5px;
  width: 11px;
  height: 20px;
}

.viewfinder.viewfinder--blue .viewfinder__card-title-rectangule {
  background-color: #a2deaf;
}

.viewfinder.viewfinder--blue .viewfinder__card-title-name {
  color: #114e4e;
}

.viewfinder.viewfinder--blue .viewfinder__card-title-job {
  color: #54585a;
}

.viewfinder.viewfinder--blue .viewfinder__icons-box {
  border: 2px solid #a2deaf;
  color: #114e4e;
}

.viewfinder.viewfinder--blue .viewfinder__icons--links {
  color: #114e4e;
}

.viewfinder.viewfinder--blue .viewfinder__tags-text {
  background-color: #438792;
  color: #ffffff;
}

.viewfinder.viewfinder--red .viewfinder__card-title-rectangule {
  background-color: #e95626;
}

.viewfinder.viewfinder--red .viewfinder__card-title-name {
  color: #420101;
}

.viewfinder.viewfinder--red .viewfinder__card-title-job {
  color: #54585a;
}

.viewfinder.viewfinder--red .viewfinder__icons-box {
  border: 2px solid #e95626;
  color: #420101;
}

.viewfinder.viewfinder--red .viewfinder__icons--links {
  color: #420101;
}

.viewfinder.viewfinder--red .viewfinder__tags-text {
  background-color: #bd1010;
  color: #ffffff;
}

.viewfinder.viewfinder--grey .viewfinder__card-title-rectangule {
  background-color: #a0c0cf;
}

.viewfinder.viewfinder--grey .viewfinder__card-title-name {
  color: #3e5b65;
}

.viewfinder.viewfinder--grey .viewfinder__card-title-job {
  color: #54585a;
}

.viewfinder.viewfinder--grey .viewfinder__icons-box {
  border: 2px solid #a0c0cf;
  color: #3e5b65;
}

.viewfinder.viewfinder--grey .viewfinder__icons--links {
  color: #3e5b65;
}

.viewfinder.viewfinder--grey .viewfinder__tags-text {
  background-color: #dfe5eb;
  color: #ffffff;
}

.main {
  background: #f5f5f5;
}

.main__header {
  width: 250px;
  margin: 0px auto 0 auto;
  padding-top: 94px;
}

.main__header--logo {
  background-image: url("../images/logo-awesome-profile-cards.png");
  width: 250px;
  height: 64px;
}

.main__intro .main__title {
  text-align: center;
  font-size: 19px;
  color: #e17334;
  font-family: 'Merriweather', serif;
  font-weight: 900;
  margin: 35.4px auto 4px auto;
}

.main__intro .main__paragraph {
  font-family: 'OpenSans-Regular',serif;
  font-size: 15px;
  font-weight: 500;
  margin: 0 auto;
  text-align: center;
  color: #114e4e;
  width: 265px;
}

.main__intro .main__list {
  display: flex;
  justify-content: space-around;
  align-items: center;
  border: 1px solid rgba(255, 115, 52, 0.21);
  width: 268px;
  height: 74px;
  margin: 10px auto;
  border-radius: 4px;
}

.main__intro .main__list-item {
  text-decoration: none;
  list-style: none;
  padding: 10px;
  text-align: center;
  color: #e17334;
}

.main__intro .main__list .main__list--text {
  color: #54585a;
  font-size: 16px;
  padding-top: 10px;
}

.main__intro .main__list .icon {
  color: #e17334;
  font-size: 28px;
}

.main__intro .main__button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px 0;
}

.main__intro .main__button--start {
  font-size: 16px;
  font-family: 'OpenSans', serif;
  width: 129px;
  height: 42px;
  background-color: #438792;
  background-color: #438792;
  border: none;
  border-radius: 21px;
  color: #ffffff;
  text-transform: uppercase;
}

.footer {
  display: flex;
  flex-direction: column;
  text-align: center;
  height: 110px;
}

.footer__copyright {
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  color: #d8d8d8;
  margin-top: 20px;
}

.footer__logo {
  margin-top: 10px;
  margin-bottom: 20px;
}

.footer__logo--image {
  width: 103px;
  height: 40px;
}

.fa-angle-up {
  color: #54585a;
}

.dropdown__desing {
  padding: 30px 20px 31px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.dropdown__desing--name {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.dropdown__desing--name .dropdown__desing--nameText {
  padding-left: 10px;
  color: #54585a;
  font-size: 18px;
  font-family: 'Open Sans';
  font-weight: 600;
  text-transform: uppercase;
}

.dropdown__desing--name .dropdown__desing--nameIcon {
  color: #e17334;
}

.dropdown__colors {
  display: flex;
  justify-content: space-between;
}

.dropdown__colors .dropdown__colors--selectors {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.dropdown__colors .dropdown__colors--selectors .colors-container {
  display: flex;
}

.dropdown__colors .dropdown__colors--selectors .colors {
  width: 30px;
  height: 25px;
}

.dropdown__colors .dropdown__colors--selectors .colors__darkCyan {
  background-color: #114e4e;
}

.dropdown__colors .dropdown__colors--selectors .colors__dirtyblue {
  background-color: #438792;
}

.dropdown__colors .dropdown__colors--selectors .colors__hospitalGreen {
  background-color: #a2deaf;
}

.dropdown__colors .dropdown__colors--selectors .colors__darkRed {
  background-color: #420101;
}

.dropdown__colors .dropdown__colors--selectors .colors__strongRed {
  background-color: #bd1010;
}

.dropdown__colors .dropdown__colors--selectors .colors__brightRed {
  background-color: #e95626;
}

.dropdown__colors .dropdown__colors--selectors .colors__darkBlue {
  background-color: #3e5b65;
}

.dropdown__colors .dropdown__colors--selectors .colors__lightBlue {
  background-color: #dfe5eb;
}

.dropdown__colors .dropdown__colors--selectors .colors__brightBlue {
  background-color: #a0c0cf;
}

.dropdown__colors .dropdown__colorsgroups {
  display: flex;
  flex-direction: row-reverse;
  padding-right: 20px;
  padding-bottom: 20px;
  align-items: center;
}

.dropdown__colors .dropdown__colorsgroups:last-child {
  padding-bottom: 32px;
}

.radio {
  margin-right: 10px;
}

.dropdown__title {
  padding-left: 39px;
  color: #54585a;
  text-transform: uppercase;
  font-family: 'Open Sans';
}

.dropdown__title h3 {
  font-size: 14px;
  font-weight: 400;
}

.dropdown__fonts {
  display: flex;
  justify-content: space-between;
}

.dropdown__fonts--selectors {
  display: flex;
  flex-direction: column;
  width: 132px;
}

.dropdown__fonts--selectors .dropdown__fontsgroups {
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-end;
  align-items: center;
  padding-bottom: 9px;
}

.dropdown__fonts--selectors .dropdown__fontsgroups:last-child {
  padding-bottom: 30px;
}

.dropdown__fonts--selectors .fonts {
  display: flex;
  align-items: flex-start;
  color: #54585a;
}

.ubuntu {
  font-family: 'Ubuntu', sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #54585a;
}

.comic {
  font-family: 'Comic Sans MS', 'Comic Sans', 'TSCu_Comic';
  font-size: 16px;
  color: #54585a;
}

.montserrat {
  font-family: 'Merriweather', serif;
  font-size: 16px;
  font-weight: 400;
  color: #54585a;
}

.bookmark {
  border: 0.5px solid #d8d8d8;
  margin-left: 20px;
  margin-right: 20px;
}

.dropdown__fill {
  margin: 10px 20px 25px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.dropdown__fill--name {
  display: flex;
  align-items: center;
}

.dropdown__fill--name .dropdown__fill--nameText {
  margin-left: 10px;
  font-size: 18px;
  font-family: 'Open Sans';
  font-weight: 600;
  text-transform: uppercase;
  color: #54585a;
}

.dropdown__fill--name .dropdown__fill--nameIcon {
  color: #e17334;
}

.fill_form--container {
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 30px;
}

.fill__form--labelinput {
  display: flex;
  flex-direction: column;
}

.fill__label--items {
  margin-bottom: 5px;
  font-family: 'Open Sans';
  font-weight: 400;
  color: #114e4e;
  font-size: 14px;
}

.fill__input--items {
  margin-bottom: 20px;
  line-height: 30px;
  border: solid 1px #a2deaf;
  padding-left: 9px;
  font-size: 14px;
}

.fill__input--buttonandminiimg {
  display: flex;
  flex-direction: row;
  margin-bottom: 20px;
}

.fill__input--buttonandminiimg .fill__input--addimgbutton {
  background-color: #e17334;
  color: #ffffff;
  font-family: 'Open Sans';
  font-size: 14px;
  width: 140px;
  height: 30px;
  border-radius: 3px;
  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.32);
}

.fill__input--buttonandminiimg .fill__input--miniimg {
  height: 30px;
  width: 30px;
  border: solid 1px #a2deaf;
  margin-left: 20px;
  background-image: url();
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.fill__form--abilitieslabelinput {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
}

.fill__input--addabilitiebutton {
  border: 1px solid #e17334;
  border-radius: 45px;
  height: 30px;
  width: 30px;
  margin-left: 20px;
}

.fill__input--addabilitiebutton .fa-minus {
  color: #e17334;
}

.action__hiddenField {
  display: none;
}

.shares__input--buttonandminiimg {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px 0 30px;
}

.shares__input--buttonandminiimg .shares__input--addimgbutton {
  background-color: #e17334;
  color: #ffffff;
  font-family: 'Open Sans';
  font-size: 18px;
  width: 199px;
  height: 50px;
  border-radius: 12px;
  box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.32);
  text-transform: uppercase;
}

.bookmark2 {
  border: 0.5px solid #d8d8d8;
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 30px;
}

@media all and (min-width: 768px) {
  .header__logo {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 40px;
  }

  .main__2 {
    display: grid;
    grid-template-columns: 1fr 369px;
  }

  .main__card {
    padding: 20px 40px 21px 0;
    grid-column: 2;
  }

  .main__form {
    grid-column-start: 1;
    grid-row: 1;
  }

  .viewfinder {
    height: 100%;
    border-radius: 6px;
    box-sizing: border-box;
  }

  .main__header {
    width: 419px;
    padding-top: 50px;
  }

  .main__header--logo {
    background-image: url("../images/logo-awesome-profile-cards-tablet.png");
    width: 419px;
    height: 107px;
  }

  .main__intro .main__title {
    font-size: 24px;
    margin: 31px auto 4px auto;
  }

  .main__intro .main__paragraph {
    font-size: 16px;
    margin: 4px auto;
    width: 278px;
  }

  .main__intro .main__list {
    border: 1px solid rgba(255, 115, 52, 0.21);
    width: 325px;
    height: 86px;
    margin: 21px auto;
  }

  .main__intro .main__list-item {
    text-decoration: none;
    list-style: none;
    padding: 10px;
    text-align: center;
  }

  .main__intro .main__button {
    padding: 100px 0 59px;
  }

  .footer {
    flex-direction: row;
    justify-content: space-between;
    height: 60px;
  }

  .footer__copyright {
    margin-left: 40px;
  }

  .footer__logo {
    margin-right: 40px;
  }
}
